import React from "react";
import { connect } from "react-redux";
import {  fetchArticles } from "../redux/actions";
import {useParams } from "react-router-dom";

import Catalog from "./Catalog";

class ArticlesDynamic extends React.Component {
  constructor(props) {
    super(props);
    this.state = { input: "", loading: true };
    let { id } = useParams();
    console.log("constructor ",id)
  }

  updateInput = input => {
    this.setState({ input });
  };

  handleAddTodo = () => {
    this.props.addTodo(this.state.input);
    this.setState({ input: "" });
  };

/*
  fetchArticles = async () => {
    var response = await fetch('https://www.markwebkitchen.com/seo/test.json');
    var data = await response.json();
console.log("fetching articles",data);
}
*/

  componentDidMount() {

 


   // this.fetchArticles()


    setTimeout(
      function () {
        this.setState({ loading: false });
      }
        .bind(this),
      3000
    );
    this.props.fetchArticles()
  }

  render() {
    return (
      <div>
        <h2>This is my Articles Component</h2>

        {this.state.loading ? <Catalog /> : <h1>Nothing here </h1>}
        {this.props.apiResults ? <h1>{this.props.apiResults}</h1> : <h1>no news </h1>}
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  apiResults: JSON.stringify(state.apiReducer.json),

})

export default connect(
  mapStateToProps,
  { fetchArticles }
)(ArticlesDynamic);




